"use client";

import Layout from "@/app/layout";
import { Label } from "@radix-ui/react-dropdown-menu";
import { Code, Option } from "lucide-react";
import { useState } from "react";
type NewFormProps={
    onSubmitRequest:(data:any)=> void;
};
export default function NewForm({onSubmitRequest}:NewFormProps) {
 
  const [localisation, setLocalisation] = useState("");
  const [formData, setFormData] = useState({
    requestNumber: "Auto Generated",
    code: "",
    client: "",
    intitule: "",
    programme: null as File | null,
    duration: "",
    startDate: "",
    endDate: "",
    address: "",
    clientPrice: "",
    comment: "",
    trainer: "",
    clientFees: "",
    logisticCost: "",
    trainerRate: "",
    status: "",
  });
  const handleLocalisationChange = (
    e: React.ChangeEvent<HTMLSelectElement>
  ) => {
    const value = e.target.value;
    setLocalisation(value);
    setFormData((prev) => ({ ...prev, address: value }));
  };
  const handleInputChange = (
    e: React.ChangeEvent<
      HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement
    >
  ) => {
    const { name, value } = e.target as
      | HTMLInputElement
      | HTMLSelectElement
      | HTMLTextAreaElement;
    setFormData((prev) => ({ ...prev, [name]: value }));
    if (name === "client") {
      const selectedClient = codeClient.find((c) => c.value === value);
      setFormData((prev) => ({
        ...prev,
        client: value,
        clientPrice: selectedClient?.tarif ?? "",
      }));
    } else if (name === "trainer") {
      const selectedTrainer = codeForamteur.find((f) => f.value === value);
      setFormData((prev) => ({
        ...prev,
        trainer: value,
        trainerRate: selectedTrainer?.tarif ?? "",
      }));
    }
  };
  const handleFileChange=(e:React.ChangeEvent<HTMLInputElement>)=>{
    const file=e.target.files?.[0] || null
    setFormData((prev)=>({... prev,programme:file}))
  }
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
onSubmitRequest(formData);
setFormData({ requestNumber: "Auto Generated",
    code: "",
    client: "",
    intitule: "",
    programme: null as File | null,
    duration: "",
    startDate: "",
    endDate: "",
    address: "",
    clientPrice: "",
    comment: "",
    trainer: "",
    clientFees: "",
    logisticCost: "",
    trainerRate: "",
    status: "",

});
console.log(formData);
// localisation.li
};
  const [codeOptions, setCodeOptions] = useState([
    { value: "code1", label: "Code 1", intitule: "intitule pour Code 1" },
    { value: "code2", label: "Code 2", intitule: "intitule pour Code 2" },
    { value: "code3", label: "Code 3", intitule: "intitule pour Code 3" },
  ]);
  const [codeClient, setCodeClient] = useState([
    { value: "client1", label: "Client 1", tarif: "321" },
    { value: "client2", label: "Client 2", tarif: "456" },
    { value: "client3", label: "Client 3", tarif: "789" },
  ]);
  const [codeForamteur, setCodeForamteur] = useState([
    { value: "Formateurx", label: "formateur X", tarif: "3000" },
    { value: "Formateury", label: "formateur Y", tarif: "4000" },
  ]);
  interface Option {
    value: string;
    label: string;
  }

  // useEffect(() => {
  //   async function fetchCodes() {
  //     const res = await fetch("/api/codes"); // Your real API endpoint
  //     const data = await res.json();
  //     setCodeOptions(data);
  //   }
  //   fetchCodes();
  // }, []);
  /* when the back is ready i should use this */

  return (
<form
          onSubmit={handleSubmit}
          className="bg-white p-6 rounded-lg w-2xl mx-auto"
        >
          <div className="border-b border-gray-200 pb-6 sm:pb-8 ">
            <label className="block text-sm font-medium text-gray-700 max-w-md mx-auto sm:max-w-lg lg:max-w-xl">
              Numéro Demande
            </label>
            <input
              type="text"
              name="requestNumber"
              className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50 "
              value={formData.requestNumber}
              readOnly
            />
          </div>
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700">
              Code
            </label>
            <select
              name="code"
              className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
              value={formData.code}
              onChange={handleInputChange}
            >
              <option value="">Sélectionnez un code</option>
              {codeOptions.map((code) => (
                <option key={code.value} value={code.value}>
                  {code.label}
                </option>
              ))}
            </select>
          </div>
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700">
              Client
            </label>
            <select
              name="client"
              className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
              value={formData.client}
              onChange={handleInputChange}
            >
              <option value="">Selectionner un client</option>
              {codeClient.map((client) => (
                <option key={client.value} value={client.value}>
                  {client.label}
                </option>
              ))}
            </select>
          </div>
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700">
              intitule
            </label>
            <input
              name="intitule"
              className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
              value={formData.intitule}
              onChange={handleInputChange}
              readOnly
            ></input>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Programme
              </label>
              <input
                type="file"
                name="programme"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                onChange={handleFileChange}
              ></input>
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Durée(jours)
              </label>
              <input
                type="number"
                name="duration"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.duration ?? ""}
                onChange={handleInputChange}
                min={1}
                placeholder="1"
              />
            </div>

            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Date Début
              </label>
              <input
                type="date"
                name="startDate"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.startDate}
                onChange={handleInputChange}
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Date Fin
              </label>
              <input
                type="date"
                name="endDate"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.endDate}
                onChange={handleInputChange}
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Lieu
              </label>
              <select
                name="address"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.address}
                onChange={handleLocalisationChange}
              >
                <option value="">Selectionner un lieu</option>
                <option value="Présentiel">Présentiel</option>
                <option value="Classe Virtuelle">Classe Virtuelle (CV)</option>
              </select>
            </div>
            {localisation === "Présentiel" && (
              <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700">
                  Adresse
                </label>
                <input
                  type="text"
                  name="address"
                  className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                  value={formData.address}
                  onChange={handleInputChange}
                />
              </div>
            )}
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Tarif
              </label>
              <input
                type="number"
                name="clientPrice"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.clientPrice}
                onChange={handleInputChange}
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Commentaire
              </label>
              <textarea
                name="comment"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                rows={2}
                placeholder="commentez ici!"
                value={formData.comment}
                onChange={handleInputChange}
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Foramteur
              </label>
              <select
                name="trainer"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.trainer}
                onChange={handleInputChange}
              >
                <option value="">Sélectionnez un formateur</option>
                {codeForamteur.map((trainer) => (
                  <option key={trainer.value} value={trainer.value}>
                    {trainer.label}
                  </option>
                ))}
              </select>
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Frais
              </label>
              <input
                type="number"
                name="clientFees"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.clientFees}
                onChange={handleInputChange}
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Coût Logistique
              </label>
              <input
                type="number"
                name="logisticCost"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.logisticCost}
                onChange={handleInputChange}
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Tarif formateur
              </label>
              <input
                type="number"
                name="trainerRate"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.trainerRate}
                onChange={handleInputChange}
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">
                Etat
              </label>
              <select
                name="status"
                className="block mt-1 w-full border-gray-300 rounded-md shadow-sm p-2 bg-gray-50"
                value={formData.status}
                onChange={handleInputChange}
              >
                <option value="">Sélectionnez un état</option>
                <option value="En cours">En cours</option>
                <option value="Traitée">Traitée</option>
                <option value="Sourcing">Sourcing</option>
                <option value="Confirmée">Confirmée</option>
                <option value="Expirée">Expirée</option>
                <option value="Annulation client">Annulation client</option>
                <option value="Annulation formateur">
                  Annulation formateur
                </option>
                <option value="Terminée">Terminée</option>
              </select>
            </div>
            <div className="mb-6">
              <button type="submit"
              className="w-full bg-blue-600 text-white p-2 rounded-md hover:bg-blue-700 transition-colors">
                Soumettre
              </button>
            </div>
          </div>
        </form>);}